<script>
export default {
  data() {
    return {
      // TODO: 1. ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏ï‡∏±‡∏ß‡πÅ‡∏õ‡∏£ placeholder
      placeholder: "Tell us your name",
      name: null,
      greetingText: "Hello! What's your name?",
      names: []
      // TODO: 7. ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏ï‡∏±‡∏ß‡πÅ‡∏õ‡∏£ names ‡∏ó‡∏µ‡πà‡πÄ‡∏õ‡πá‡∏ô array ‡∏Ç‡∏≠‡∏á name
    };
  },
  methods: {
    // TODO: 3. Say hello! ‡πÇ‡∏î‡∏¢‡∏Å‡∏≤‡∏£‡∏ô‡∏≥‡∏ä‡∏∑‡πà‡∏≠‡∏ó‡∏µ‡πà‡∏ú‡∏π‡πâ‡πÉ‡∏ä‡πâ‡∏Å‡∏£‡∏≠‡∏Å‡∏°‡∏≤‡∏£‡∏ß‡∏°‡πÉ‡∏ô greetingText
    enter() {
      if (!this.name) return;
      this.greetingText = "Hello, " + this.name + "!";
      // TODO: 6. reset name ‡πÄ‡∏°‡∏∑‡πà‡∏≠ enter
      this.name = null;
    },
    // TODO: 8. ‡πÄ‡∏û‡∏¥‡πà‡∏° function add()
    add() {
      if (!this.name) return;
      this.names.push(this.name);
